
.z-store-item {
  width: 49.5%;
  background: #fff;
  float: left;
  margin: 0 1% 1% 0;
}
.z-store-item:nth-child(even) {
  margin-right: 0;
}
.store-img {
  height: 3.45rem;
  width: 100%;
}
.back-img {
  background-position: center center;
  background-size: cover;
}
.store-desc {
  padding: 0.2rem;
}
.storeSale {
  font-size: 0.23rem;
}



