
.login {
  background: #fff;
}
.login-box {
  width: 5.6rem;
  margin: 0.8rem auto;
}
.login-group {
  margin-bottom: 0.4rem;
  padding: 0 0.1rem 0.2rem;
}
.login-box .weui-cell {
  padding: 0;
}
.login-box .weui-cell:before {
  border-top: 0;
}
.login-button-box {
  width: 5.45rem;
  margin: 0 auto;
}
.login-button {
  border-radius: 99px;
}
.weui-btn_default {
  color: #fff!important;
  background-color: #f9261d!important;
}
.weui-btn_default:active {
  background-color: #ccc!important;
}
.forger-pass {
  text-align: right;
  padding-top: 0.2rem;
}
.forger-pass a {
  color: #2cadff;
}



