
button {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border: none;
  border-radius: 4px;
  box-shadow: inset 1px 1px 1px rgba(255, 255, 255, 0.3), 0 1px 1.5px rgba(0, 0, 0, 0.15), 0 1px 1px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #bcdffb;
}
.button {
  height: 100%;
  padding: 2em;
  position: relative;
  width: 100%;
}
.button.styl-material {
  transition: 200ms background cubic-bezier(0.4, 0, 0.2, 1);
  background: #2196F3;
}
.button.styl-material:hover,
.button.styl-material:focus {
  outline: none;
  background: #2b9bf4;
}
.ripple-obj {
  height: 100%;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  fill: #0c7cd5;
}
.ripple-obj use {
  opacity: 0;
}
.scrollbar-track-x {
  display: none !important;
}
.z-index-category .category-items {
  padding-right: 0.1rem;
}
.z-index-category .category-items:last-child {
  padding-right: 0;
}
.z-index-category .category-items img {
  border-radius: 6px;
  width: 2.2rem;
  height: 2.3rem;
}
.z-index-category .items-title {
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.z-index-category .items-price {
  color: #ee5e56;
}
.z-index-category .items-store {
  color: #b8b8b8;
}
.z-index-category .items-store .store-name {
  padding-left: 0.05rem;
}
.z-category-header {
  border-left: 0.1rem solid #2a96f9;
  margin-bottom: 0.25rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  padding-left: 0.15rem;
}
.z-category-header:active {
  background-color: #ECECEC;
}
.z-arrow-right {
  display: inline-block;
  height: 6px;
  width: 6px;
  border-width: 2px 2px 0 0;
  border-color: #C8C8CD;
  border-style: solid;
  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
          transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  top: -2px;
  top: 0;
}



