
.menu-item {
  width: 1.4rem;
  padding: 0.2rem 0;
  text-align: center;
  margin-right: 10px;
  background: transparent;
  box-shadow: none;
  color: #000;
  border-radius: 0;
}
.menu-item:last-child {
  margin-right: 0;
}
.menu-item.red {
  color: #f9261d;
  border-bottom: 1px solid #f9261d;
}
.zmenu {
  background: #fff;
}



