
.menu-box {
  background: #fff;
  overflow: hidden;
}
.store-title {
  padding-left: 0.2rem;
}
.orderList-detail {
  margin: 0.2rem 0;
  background: #f8f8f8;
  padding: 0.2rem;
}
.orderList-time {
  padding-bottom: 0.2rem;
}
.orderList-time .time {
  font-size: 0.21rem;
}
.orderList-btns {
  padding-top: 0.2rem;
  text-align: right;
}
.orderList-btns .z-check-button {
  margin-right: 0.1rem;
  border-radius: 0;
  border-color: #f8f8f8;
}
.orderList-btns .z-check-button.active {
  color: #f9261d;
  border-color: #f9261d;
}
.orderList-btns .z-check-button.active:active {
  color: #f9261d!important;
  border-color: #f9261d!important;
  background-color: transparent!important;
}



