* {
  box-sizing: border-box;
}
body,
html {
  height: 100%;
  font-size: 0.28rem;
  background-color: #f4f4f4;
}
body {
  max-width: 720px;
  margin: 0 auto;
  position: relative;
}
#app {
  position: absolute;
  width: 100%;
  height: 100%;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-leave-active,
.fade-enter {
  opacity: 0;
}
.zflex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
              -ms-flex-align: center;
          align-items: center;
}
.zflex-col {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
              -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
              -ms-flex-align: center;
          align-items: center;
}
.zflex-center {
  -webkit-box-pack: center;
              -ms-flex-pack: center;
          justify-content: center;
}
.zflex1 {
  -webkit-box-flex: 1;
              -ms-flex: 1;
          flex: 1;
}
.zicon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  overflow: hidden;
  position: relative;
  vertical-align: middle;
}
a img {
  width: 100%;
  border: 0;
}
a {
  color: #000;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.animated.hinge {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
}
/*the animation definition*/
@-webkit-keyframes fadeInUp {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
@keyframes fadeInUp {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 5%, 0);
    transform: translate3d(0, 5%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
@-webkit-keyframes fadeInDown {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
@keyframes fadeInDown {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
.z-gray {
  color: #C7C7C7;
}
.z-red {
  color: #f9261d;
}
.z-container .weui-cells {
  margin-top: 0.2rem;
}



