
button {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border: none;
  border-radius: 4px;
  box-shadow: inset 1px 1px 1px rgba(255, 255, 255, 0.3), 0 1px 1.5px rgba(0, 0, 0, 0.15), 0 1px 1px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #bcdffb;
}
.button {
  height: 100%;
  padding: 2em;
  position: relative;
  width: 100%;
}
.button.styl-material {
  transition: 200ms background cubic-bezier(0.4, 0, 0.2, 1);
  background: #2196F3;
}
.button.styl-material:hover,
.button.styl-material:focus {
  outline: none;
  background: #2b9bf4;
}
.ripple-obj {
  height: 100%;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  fill: #0c7cd5;
}
.ripple-obj use {
  opacity: 0;
}
.mu-refresh-control {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  width: 40px;
  height: 40px;
  -webkit-box-align: center;
              -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
              -ms-flex-pack: center;
          justify-content: center;
  background-color: #FFF;
  border-radius: 50%;
  position: absolute;
  left: 0;
  right: 0;
  box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
  top: 4rem;
  z-index: 90;
}
.mu-refresh-control .mu-icon {
  display: inline-block;
  vertical-align: middle;
}
.zoom-enter-active {
  -webkit-animation: zoomIn .5s;
          animation: zoomIn .5s;
}
.zoom-leave-active {
  -webkit-animation: zoomOut .5s;
          animation: zoomOut .5s;
}
@-webkit-keyframes zoomIn {
0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
50% {
    opacity: 1;
}
}
@keyframes zoomIn {
0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
50% {
    opacity: 1;
}
}
@-webkit-keyframes zoomOut {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
100% {
    opacity: 0;
}
}
@keyframes zoomOut {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
100% {
    opacity: 0;
}
}



