
.z-arrow {
  height: 0.5rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.z-arrow .leftwords {
  font-size: 15px;
  color: #ccc;
}
.z-header {
  font-size: 19px;
  height: 0.85rem;
  text-align: center;
  padding: 0.08rem 0.2rem;
  box-sizing: border-box;
  background: #fff;
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 100;
  left: 0;
  transition: all ease 0.3s;
}
.z-header.vux-1px-b {
  position: absolute;
}
.z-header-left {
  position: relative;
}
.z-header-right {
  -webkit-box-pack: end;
              -ms-flex-pack: end;
          justify-content: flex-end;
}
.zheader-icon {
  font-size: 0.45rem;
}
.z-header-middle {
  width: 3.8rem;
}
.zheader-icon-word {
  font-size: 12px;
}



