
.z-hori-menu {
  white-space: nowrap;
}
.z-hori-menu .z-check-button {
  border-color: #C7C7C7;
  margin-right: 10px;
}
.z-hori-menu .z-check-button:last-child {
  margin-right: 0;
}
.z-hori-menu .z-check-button.active {
  color: #f9261d;
  border-color: #f9261d;
}
.z-hori-menu .z-check-button:active {
  color: #f9261d!important;
  border-color: #f9261d!important;
  background-color: transparent!important;
}
.weui-btn + .weui-btn {
  margin-top: 0 !important;
}



