
.tf-carousel-containner {
    overflow: hidden;
    position: relative;
    height: 100%;
}
.tf-carousel-box {
    height: 100%;
    position: absolute;
    z-index: 10;
}
.carousel-img {
    background-position: center center;
    background-size: cover;
    height: 100%;
    display: inline-block;
}
.tf-carousel-nav {
    position: absolute;
    bottom: 6px;
    right: 10px;
    z-index: 11;
}
.tf-carousel-nav a {
    display: inline-block;
    background-color: #fff;
    cursor: pointer;
    width: 6px;
    height: 6px;
    border-radius: 5px;
    border: 1px solid #808080;
    /*transition: all .5s ease-in;*/
    margin-left: 3px;
}
.tf-carousel-nav a.active {
    background-color: #ea2a1d;
}



