
.user-header-top {
  background: transparent;
  color: #fff;
}
.user-header {
  background: #e53549;
  height: 3rem;
  margin-top: -0.85rem;
}
/*.z-page.z-user-index{
    position:relative;
}*/
.user-header-center {
  padding-top: 0.85rem;
  height: 2.15rem;
  padding-left: 0.3rem;
}
.avatar {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
}
.username {
  padding-left: 0.3rem;
  color: #fff;
  font-size: 0.35rem;
}
.account-manage {
  padding: 0.1rem 0.4rem 0.1rem 0.2rem;
  border-radius: 10px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
}
.user-focus {
  background: #fff;
}
.user-focus .zicon {
  margin: 0;
}
.user-focus .user-focus-iem {
  padding: 0.15rem 0;
}
.user-focus-desc {
  padding-left: 0.2rem;
}
.user-focus-desc div {
  line-height: 1.3;
}
.itembox {
  padding: 0.15rem 0;
  position: relative;
  background: #fff;
}
.reddot {
  width: 0.1rem;
  height: 0.1rem;
  border-radius: 50px;
  background: #f74c31;
  position: absolute;
  top: 0.05rem;
  right: 0.6rem;
}
.my-order .weui-cells {
  margin-top: 0;
}



