
.z-checkbox .zicon {
  margin: 0;
}
.check-label {
  position: relative;
}
.boxcheck {
  display: none;
}
.z-check {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 11;
}
.z-check:checked + .boxcheck {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.z-check:checked + .boxcheck + .boxnocheck {
  display: none;
}
.checkbox-miidle {
  padding-left: 0.2rem;
}



