
.cart-box{
    margin-top: 0.2rem;
}
.cart-item{
    padding: 0.2rem 0.3rem;
    background: #fff;
}
.cart-title{
    padding-left:0.2rem;
}
.cart-right{
    padding: 0.1rem 0 0.1rem 0.2rem;
}
.service-img{
    width:1.25rem;
    height:1.25rem;
    background-size: cover;
    background-position: center center;
}
.service-name{
    padding-left: 0.2rem;
}
.service-title{
    padding-bottom: 0.4rem;
}
.z-cart .buynow{
    width: 2.5rem;
}
.service-bottom .cart-total{
    text-align: left;
    padding-left:0.3rem;
}



