
.z-verti-menu {
  width: 1.65rem;
  background: #fff;
  height: 100%;
  -webkit-overflow-scrolling: touch;
}
.verti-item {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  background: transparent;
  box-shadow: none;
  color: #000;
  border-radius: 0;
}
.verti-item.checked {
  border-left: 2px solid #e74037;
}



