
.z-container {
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding-top: 0.85rem;
  padding-bottom: 0.85rem;
}
.z-page {
  position: absolute;
  height: 100%;
  width: 100%;
}
.z-carousel-box {
  height: 3.85rem;
}
.z-cate-container {
  padding: 0 0.3rem 0.3rem;
  background-color: #fff;
}
.z-cate-box {
  padding-top: 0.3rem;
}
.z-box {
  padding: 0.15rem 0.2rem;
  background-color: #fff;
  margin-top: 0.18rem;
  overflow: hidden;
}
.gray{
  color:rgb(150, 150, 150)
}
.red{
  color:#f9261d;
}



