
.z-edit-user .user-header-center {
  padding-left: 0;
}
.z-edit-user .avatar {
  margin: 0 auto;
}
.avatar-div {
  width: 1.2rem;
  height: 1.2rem;
  position: relative;
}
.uploader-input {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.weui-select {
  -webkit-appearance: none;
  border: 0;
  outline: 0;
  background-color: transparent;
  width: 100%;
  font-size: inherit;
  position: relative;
  z-index: 1;
  padding-left: 15px;
}



